/**
 *
 * Radio Button and Checkbox
 *
 * @author Takuto Yanagida
 * @version 2021-12-02
 *
 */


@use 'common';


%rad-chk-base {
	appearance: none;
	margin    : 0;

	cursor: pointer;
	font: inherit;

	width : 1em;
	height: 1em;

	transform: translateY(0.1em);

	position: relative;
	&::before,
	&::after {
		content: '';
		color: currentColor;

		position : absolute;
		top      : 50%;
		left     : 50%;
		transform: translate(-50%, -50%);
	}
	&::before {
		background-color: var(--nc-form-background-color);
		border          : var(--nc-form-border);
	}
	@extend %focus;

	&:disabled {
		--nc-form-mark-color: var(--nc-form-disabled-color);
		--nc-form-border    : var(--nc-form-border-disabled);

		cursor: not-allowed;
	}
}

@mixin radio {
	@extend %rad-chk-base;

	border-radius: 50%;

	&::before,
	&::after {
		width        : 1em;
		height       : 1em;
		border-radius: 50%;
	}
	&::after {
		border : 0.33em solid var(--nc-form-mark-color);
		opacity: 0;
	}
	&:checked::after {
		opacity: 1;
	}
}

@mixin checkbox {
	@extend %rad-chk-base;

	border-radius: 2px;

	&::before {
		width        : 1em;
		height       : 1em;
		border-radius: 2px;
	}
	&::after {
		width : 0.75em;
		height: 0.75em;

		box-shadow: inset 0 0 0 2em var(--nc-form-background-color);  // For print
		clip-path : polygon(14% 44%, 0 65%, 55% 100%, 100% 12%, 78% 0%, 45% 64%);
		opacity   : 0;
	}
	&:checked {
		&::before {
			background-color: var(--nc-form-mark-color);
			border-color    : var(--nc-form-mark-color);
			box-shadow      : inset 0 0 0 2em var(--nc-form-mark-color);  // For print
		}
		&::after {
			opacity: 1;
		}
	}
}
